

.ticker-container {
    text-align: center;
    text-align: -webkit-center;
}

.up-down-percent{
    display: inline-block;
}

.up-color{
    color: #7eff10;
}
.down-color{
    color: #ff5d28;
}

.bracket-open, .bracket-close
{

    display: inline-block;

}
div.up-down-icon-each {
    display: inline-block;
    margin-right: 2px;
}

.bracket-close
{
    margin-left: 2px;
}
.eachticker-header {
    display: block;
    text-align: left;
}
.bracket-open
{
    margin-right: 2px;
}
.anatomy-boundry {
    border: 2px solid;
    border-radius: 10px;
}


.totalVal-div{
    text-align: left;
    margin-top: 5px;
    font-size: 0.9rem;
}

#totalValpercent{
    margin-right: 5px;
    display: inline-block;
}
div#totalVal {
    display: inline-block;
}


.values-div{
    display: inline-block;
}

.addstock-div{
    vertical-align: top;
    font-size: 1.2rem;
}

.positivecolorcode{
    color: #7eff10;
}
.negativecolorcode{
    color: rgb(255, 93, 40);
}

.remove-holder {
    display: inline-block;
}
div#icon-delete {
    color: #ff5600;
}

.remove-holder {
    right: -5px;
    top: -5px;
    position: relative;
}


