/* Portrait and Landscape */

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {

}

@media only screen and (min-device-width: 481px) and (max-device-width: 767px) {

}

@media only screen and (min-device-width: 320px) and (max-device-width: 767px) {
    .appLogo-holder{
        width: 100%;
    }
    .cp-div {
        margin: 0px 20px;
    }
    img.avtarStyle {
        width: 100%;
        border-radius: 50%;
    }
    .avtarholder {
        padding: 10px;
        width: 10%;
        display: inline-block;
    }
    .only-mobile-menu {
        display: inline-block;
        vertical-align: top;
        text-align: end;
        width: 80%;
        padding-top: 15px;
    }
    div#iconName {
        display: inline-block;
        width: 90%;
        font-size: 1rem;
        border: 1px solid #077fc7;
        border-radius: 5px;
        padding: 10px 0px;
    }
    div#icon-setting {
        display: none;
    }
    .icon-div{
        text-align: center;
        margin: 10px 0px;
        cursor: pointer;
    }
    .hemburger-menu {
        padding: 10px 0px;
    }
    .display-none-class{
        display: none;
    }
    .roundbracket{
        display: none;
    }
    div#cp-label {
        font-size: 1.5rem;
        margin-right: 20px;
    }

    .ac-value{
        font-size: 1.5rem;
        margin-right: 10px;
        color: #4ee4ff;
    }
    .eachtickerblock{
        display: block;
        height: auto;
        padding: 5px;
        background-color: #05345030;
        position: relative;
        -webkit-box-shadow: 1px 2px 4px rgba(255, 255, 255, 0.5);
        box-shadow: 4px 4px 4px 1px rgba(179, 179, 179, 0.5);
        padding: 10px;
        margin: 1%;,
        cursor: pointer;
    }
    .addstock-label {
        margin-top: 5px;
        font-size: 1.2rem;
        display: inline-block;
    }
    .addstock-icon {
        display: inline-block;
    }
    .stockAnalytics-holder{
        width: 100%;
        display: block;
        vertical-align: top;
        margin-left: 0;
        margin-right: 0;
    }
    .mainBodyleft {
        display: inline-block;
        width: 100%;
        vertical-align: top;
        margin: 10px 0px;
    }
    .mobile-only {
        display: inline-block;
    }
    .desktop-only-inline{
        display: none;
    }
    .tickername{
        width: 15%;
        margin-bottom: 5px;
        text-align: -webkit-left;
        padding-left: 5px;
        display: inline-block;
        margin-right: 5px;
        font-size: 1.2rem;

    }
    .tickerprice{
        font-size: 1rem;
        margin-bottom: 5px;
        margin-top: 5px;
    }
    .change-container-div{
        font-size: 0.9rem;
        margin-left: 5px;
    }
    .desktop-only-price{
        display: none;
    }
    .mobile-only-price{
        display: inline-block;
    }
    .mobile-price-style{
        font-size: 0.9rem;
        margin-left: 5px;
    }
    .mobile-only-price-style{
        width: 75%
    }
    .span-lbl-gain {
        display: inline-block;
        margin-right: 5px;
        color: #41fcff;
    }
    .total-value-style{
        margin-top: 5px;
    }
    .desktiop-only-add-icon{
        display: none;
    }
    .mobile-only-add-icon{
        display: block;
    }
    .mainBodyright {
        display: block;
        width: 90%;
        font-size: 1rem;
        vertical-align: top;
        margin: 20px;
    }
    .anatomy-boundry {
        margin: 15px;
    }
    .stats-div {
        /* width: 100%; */
        text-align: -webkit-center;
        position: relative;
        left: 0px;
    }
    .movingavg-div, .stats-div-width{
        width: 100%;
        vertical-align: top;
    }
    .left-anatomy-div {
        display: block;
        width: 100%;
        vertical-align: top;
    }
    .movingavg-div{
        margin-left: 0px;
    }
    .close-btn {
        text-align: -webkit-right;
        text-align: right;
        display: inline-block;
        /* margin-bottom: 10px; */
        position: absolute;
        top: 95px;
        background-color: #ff7835;
        border-radius: 10px;
        padding: 0px;
        cursor: pointer;
        right: 10px;
    }
}


@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {

}

@media only screen and (min-device-width: 1025px){
    .appLogo-holder{

        width: 9%;
        display: inline-block;
        background-color: #05345030;
        height: 100vw;
    }
    img.avtarStyle {
        width: 100%;
        border-radius: 50%;
    }
    .avtarholder {
        margin: 10px;
    }
    .only-mobile-menu {
        display: none;
    }
    .icon-div{

        text-align: center;
        margin: 20px 0px;
        cursor: pointer;
    }
    .roundbracket{
        margin: 20px 0px;
        display: inline-block;
        font-size: 1.8rem;
    }
    div#cp-label {
        font-size: 2rem;
        margin-right: 20px;
    }

    .ac-value{
        font-size: 2rem;
        margin-right: 10px;
        color: #4ee4ff;
    }
    .eachtickerblock{
        display: inline-block;
        width: 11vw;
        height: 135px;
        padding: 5px;
        background-color: #05345030;
        position: relative;
        -webkit-box-shadow: 1px 2px 4px rgba(255, 255, 255, 0.5);
        box-shadow: 4px 4px 4px 1px rgba(179, 179, 179, 0.5);
        padding: 10px;
        margin: 1%;,
        cursor: pointer;
    }
    .addstock-label {
        margin-top: 5px;
        font-size: 1.7rem;
    }
    .stockAnalytics-holder{
        width: 89.9%;
        display: inline-block;
        vertical-align: top;
        margin-left: 5px;
        margin-right: 5px;
    }
    .mainBodyleft {
        display: inline-block;
        width: 70%;
        vertical-align: top;
        margin: 10px 20px;
    }
    .mobile-only {
        display: none;
    }
    .desktop-only-inline{
        display: inline-block;
    }
    .tickername{
        width: 75%;
        margin-bottom: 5px;
        text-align: -webkit-left;
        padding-left: 5px;
        display: inline-block;
        margin-right: 5px;
        font-size: 1.2rem;

    }
    .tickerprice{
        font-size: 1rem;
        margin-bottom: 5px;
        margin-top: 5px;
    }
    .change-container-div{
        font-size: 0.9rem;
        margin-left: 5px;

    }
    .mobile-only-price{
        display: none;
    }
    .desktop-only-price{
        display: inline-block;
    }
    .mobile-price-style{
        font-size: 0.9rem;
        margin-left: 5px;
    }
    .span-lbl-gain {
        color: #41fcff;
        vertical-align: top;
    }
    .desktiop-only-add-icon{
        display: block;
    }
    .mobile-only-add-icon{
        display: none;
    }
    .mainBodyright {
        display: inline-block;
        width: 25%;
        font-size: 1rem;
        vertical-align: top;
    }
    .stats-div {
        /* width: 100%; */
        text-align: -webkit-center;
        position: relative;
        left: 25px;
    }
    .movingavg-div, .stats-div-width{
        width: 47.5%;
        vertical-align: top;
    }
    .left-anatomy-div {
        display: inline-block;
        width: 93%;
        vertical-align: top;
    }
    .movingavg-div{
        margin-left: 15px;
    }
    .close-btn {
        text-align: -webkit-right;
        text-align: right;
        display: inline-block;
        /* margin-bottom: 10px; */
        position: relative;
        top: -17px;
        background-color: #ff7835;
        border-radius: 10px;
        padding: 0px;
        cursor: pointer;
        right: -20px;
    }
}



